<!-- @format -->

<template>
  <div class="row">
    <form action="" @submit.prevent class="col-4 offset-4">
      <div :class="{ error: errors.title !== '' }">
        <label for="Title">Title</label>
        <input
          type="text"
          maxlength="20"
          placeholder="text here"
          v-model="title"
           @keyup="checkKeyup"
        />
        <div>{{ errors.title }}</div>
      </div>
      <div class="my-5" :class="{ error: errors.description !== '' }">
        <label for="Title">Description</label>
        <textarea
          name=""
          id=""
          cols="30"
          rows="1"
          v-model="description"
          @keyup="checkKeyup"
        ></textarea>
        <div>{{ errors.description }}</div>
      </div>
      <div :class="{ error: errors.link !== '' }">
        <label for="Title">Link</label>
        <input type="url" pattern="https://.*"  @keyup="checkKeyup" required v-model="link" />
        <div>{{ errors.link }}</div>
      </div>
      <div class="d-flex justify-content-center mt-5">
        <base-button class="ms-3" @click.native="addCourses">Thêm</base-button>
        <router-link to="/">
          <base-button class="ms-3">Về trang chủ</base-button>
        </router-link>
      </div>
    </form>
  </div>
</template>
<script>
import BaseButton from "./BaseButton.vue";
import validInput from "../mixins/validInput";

export default {
  components: { BaseButton },
  name: "AddItems",
  mixins: [validInput],
  methods: {
    addCourses() {
      this.checkError();
      this.isSubmit = true
      if (
        this.errors.title !== "" ||
        this.errors.description !== "" ||
        this.errors.link !== ""
      )
        return;
      else {
        let newCourses = {
          id: new Date(),
          title: this.title,
          description: this.description,
          link: this.link,
        };
        this.courses.push(newCourses);
        if (!localStorage.getItem("courses")) {
          localStorage.setItem("courses", JSON.stringify(this.courses));
        } else {
          let allCourses = JSON.parse(localStorage.getItem("courses"));
          allCourses.unshift(newCourses);
          localStorage.setItem("courses", JSON.stringify(allCourses));
        }
        this.$router.push({ path: "/view_courses" });
      }
    },
  },
};
</script>
<style lang="scss" scoped>
form {
  color: white;
  input,
  textarea {
    outline: none;
    border: none;
    background: transparent;
    border-radius: 5px;
    border-bottom: 2px solid rgb(255, 255, 255);
    width: 100%;
    padding-bottom: 0.5rem;
    color: inherit;
    &::-webkit-input-placeholder {
      color: white;
      opacity: 0.8;
    }
    &:-ms-input-placeholder {
      color: white;
      opacity: 0.8;
    }
    &::placeholder {
      color: white;
      opacity: 0.8;
    }
  }
  .error {
    input,
    textarea {
      border-color: rgb(255, 53, 53);
    }
    div {
      color: rgb(255, 85, 85);
    }
  }
}
</style>
