<template>
  <div class="row view-courses justify-content-center py-5">
    <div class="col-4 mt-3" v-for="course in allCourses" :key="course.id">
      <div class="course">
        <div class="d-flex">
          <strong>{{ course.title }}</strong>
          <div class="d-flex ms-auto">
            <button class="btn btn-danger me-2" @click="removeItems(course.id)">
              delete
            </button>
            <router-link :to="'/edit_course/' + course.id"
              ><button class="btn btn-warning">edit</button></router-link
            >
          </div>
        </div>
        <div class="my-2">{{ course.description }}</div>
        <div><a :href="course.link" target="_blank">View Course</a></div>
      </div>
    </div>
    <div class="d-flex mx-auto justify-content-center mt-5">
      <router-link to="/add_courses">
        <base-button>Thêm khóa học</base-button>
      </router-link>
      <router-link to="/">
        <base-button class="ms-3">Về trang chủ</base-button>
      </router-link>
    </div>
  </div>
</template>
<script>
import BaseButton from "./BaseButton.vue";
export default {
  components: { BaseButton },
  data() {
    return {
      allCourses: [],
    };
  },
  methods: {
    removeItems(id) {
      let itemRemove = this.allCourses.find((item) => item.id === id);
      this.allCourses.splice(this.allCourses.indexOf(itemRemove), 1);
      localStorage.setItem("courses", JSON.stringify(this.allCourses));
    },
  },
  mounted() {
    setTimeout(() => {
      this.allCourses = JSON.parse(localStorage.getItem("courses"));
    }, 100);
  },
};
</script>
<style lang="scss" scoped>
.course {
  background: wheat;
  border-radius: 10px;
  padding: 1rem;
  height: 100%;
}
</style>