/** @format */

export default {
  data() {
    return {
      title: "",
      description: "",
      link: "",
      isSubmit : false,
      courses: [],
      errors: {
        title: "",
        description: "",
        link: "",
      },
    };
  },
  methods: {
    checkError() {
      if (this.title.trim().length < 1) {
        this.errors.title = "please enter a title";
      } else {
        this.errors.title = "";
      }
      if (this.description.trim().length < 1) {
        this.errors.description = "please enter a description";
      } else {
        this.errors.description = "";
      }
      if (this.link.trim().length < 1 || this.link.slice(0, 8) !== "https://") {
        this.errors.link = "please enter a link";
      } else {
        this.errors.link = "";
      }
    },
    checkKeyup() {
        if(this.isSubmit) {
            this.checkError()
        }
    }
  },
};
